import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';



@Injectable()
export class RolePermissionService {

  constructor(private http: HttpClient) { }

  getAllPermissions() {
    return this.http.get(environment.url + 'api/Permissions/PermissionByGrouping');
  }

  deleteRoleById(roleId) {
    return this.http.delete(environment.url + 'api/Roles/' + roleId);
  }

  getAllRolePermissions() {
    return this.http.get(environment.url + 'api/Roles');
  }

  submitPermissionsForRole(payload) {
    return this.http.post(environment.url + 'api/Roles/PermissionsForRole', payload);
  }

  existingPermissionsofRole(roleId) {
    return this.http.get(environment.url + 'api/Roles/PermissionsOfRole/' + roleId);
  }



}
