import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { RoleService } from '../role.service';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-role-add',
  templateUrl: './role-add.component.html',
  styleUrls: ['./role-add.component.scss'],
  providers: [RoleService]
})
export class RoleAddComponent implements OnInit {

  roleForm = new FormGroup({
    'roleName': new FormControl(''),
    'roleDescription': new FormControl('')
  })

  constructor(private service: RoleService, private router: Router, private ngxService: NgxUiLoaderService) { }

  ngOnInit() {
  }

  addRole(fg: FormGroup) {
    this.ngxService.start();
    var roleName = fg.value.roleName;
    var description = fg.value.roleDescription;

    var payload = {
      roleName,
      description
    }
    this.service.addNewRole(payload).subscribe((response: any) => {
      if (response > 0) {
        this.ngxService.stop();
        Swal.fire(
          'Success',
          'Role Added Successfully.',
          'success'
        )
        this.router.navigate(['/settings']);
      }
    });
  }

}
