import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ForgotpasswordComponent } from './forgotpassword/forgotpassword.component';
import { RoleAddComponent } from './role/role-add/role-add.component';
import { RoleEditComponent } from './role/role-edit/role-edit.component';
import { RolePermissionAddComponent } from './role-permission/role-permission-add/role-permission-add.component';
import { RolePermissionEditComponent } from './role-permission/role-permission-edit/role-permission-edit.component';
import { ExternalUserAddComponent } from './external-user/external-user-add/external-user-add.component';
import { ExternalUserEditComponent } from './external-user/external-user-edit/external-user-edit.component';
import { ExternalUserListComponent } from './external-user/external-user-list/external-user-list.component';
import { ExternalUserDetailComponent } from './external-user/external-user-detail/external-user-detail.component';

const routes: Routes = [
	{ 
		path: 'forgot-password', 
		component: ForgotpasswordComponent 
	},
	{ 
		path: 'role-add', 
		component: RoleAddComponent 
	},
	{ 
		path: 'role-edit', 
		component: RoleEditComponent 
	},
	{ 
		path: 'role-permission-add', 
		component: RolePermissionAddComponent 
	},
	{ 
		path: 'role-permission-edit', 
		component: RolePermissionEditComponent 
	},

	{
		path: 'external-user-add', 
		component: ExternalUserAddComponent
	},
	{
		path: 'external-user-edit', 
		component: ExternalUserEditComponent
	},
	{
		path: 'external-user-list', 
		component: ExternalUserListComponent
	},
	{
		path: 'external-user-detail', 
		component: ExternalUserDetailComponent
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class AppUsersRoutingModule { }
