import { ExternalUserService } from './../externaluser.service';
import { Component, OnInit, ElementRef, ViewChild, ChangeDetectorRef } from '@angular/core';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-external-user-list',
  templateUrl: './external-user-list.component.html',
  styleUrls: ['./external-user-list.component.scss'],
  providers: [ExternalUserService]
})
export class ExternalUserListComponent implements OnInit {

  registrationStatus = [];
  registrations = [];

  @ViewChild('dataTable') table: ElementRef;
  dataTable: any;

  constructor(private service: ExternalUserService, private chRef: ChangeDetectorRef) {

    this.service.getRegistrationStatus().subscribe((response: any) => {
      this.registrationStatus = response;
    })

    this.getAllregistrations();
  }

  ngOnInit() {
  }

  getAllregistrations() {
    this.service.getRegistrationsByStatus(0).subscribe((response: any) => {
      this.registrations = response;
    })

  }

  filterStatusValue(id) {
    this.service.getRegistrationsByStatus(id).subscribe((response: any) => {
      this.registrations = response;
    })
  }

  changeStatusValue(registrationId, statusId) {
    var payload = {
      id: registrationId,
      appUserStatusId: statusId,
    }

    this.service.updateRegistrationStatus(payload).subscribe((response) => {
      if (response == true) {
        Swal.fire({
          type: 'success',
          title: 'Success',
          text: 'Status Changed',
        });
      }
    })
  }

}
