import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable()
export class SettingService {

  constructor(private http: HttpClient) { }

  getAllRoles() {
    return this.http.get(environment.url + 'api/Roles');
  }

  deleteRoleById(roleId) {
    return this.http.delete(environment.url + 'api/Roles/' + roleId);
  }

  getAllRolePermissions() {
    return this.http.get(environment.url + 'api/Roles');
  }

  getAllPermissions() {
    return this.http.get(environment.url + 'api/Permissions/PermissionByGrouping');
  }

  getPermissionOfRole(id) {
    return this.http.get(environment.url + `api/Roles/PermissionsOfRole/${id}`)
  }

  getAllInternalUsers() {
    return this.http.get(environment.url + `api/AppUsers/Internal`)
  }

  palikaSync(payload) {
    return this.http.post(environment.url + `api/AppUsers/HamroPalika/Sync`, payload)
  }

  updateInternalUserRole(userId, payload) {
    return this.http.post(environment.url + `api/AppUsers/${userId}/Roles`, payload);
  }

  getAppUserById(id) {
    return this.http.get(environment.url + `api/AppUsers/${id}`)
  }

  addFiscalYear(payload) {
    return this.http.post(environment.url + `api/Company/FiscalYears`, payload);
  }

  getAllFiscalYear() {
    return this.http.get(environment.url + `api/Company/FiscalYears`)
  }

}
