import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class ExternalUserService {

  constructor(private http: HttpClient) { }

  registerExternalUser(payload) {
    return this.http.post(environment.url + `api/AppUsers/External/Registrations`, payload);
  }

  getRegistrationStatus() {
    return this.http.get(environment.url + 'api/AppUsers/External/Registrations/Statuses');
  }

  getRegistrationsByStatus(registrationStatusId) {
    return this.http.get(environment.url + `api/AppUsers/External/Registrations/Statuses/${registrationStatusId}`);
  }

  updateRegistrationStatus(payload) {
    return this.http.post(environment.url + `api/AppUsers/External/Registrations/Verify`, payload);
  }

}
