import { ExternalUserService } from './../externaluser.service';
import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';
import { FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-external-user-add',
  templateUrl: './external-user-add.component.html',
  styleUrls: ['./external-user-add.component.scss'],
  providers: [ExternalUserService]
})
export class ExternalUserAddComponent implements OnInit {

  externalUserAddForm;

  constructor(private fb: FormBuilder, private service: ExternalUserService, private router: Router) { }

  ngOnInit() {
    this.externalUserAddForm = this.fb.group({
      firstName: ['', Validators.required],
      middleName: [''],
      lastName: ['', Validators.required],
      permanentAddress: ['', Validators.required],
      temporaryAddress: [''],
      affiliatedOrganizationName: [''],
      position: [''],
      contactNumber: ['', Validators.required],
      emailAddress: ['']
    })
  }

  onExternalUserSubmit() {
    console.log(this.externalUserAddForm.value)
    if (this.externalUserAddForm.invalid) {
      return;
    }
    this.service.registerExternalUser(this.externalUserAddForm.value).subscribe((response) => {
      if (response > 0) {
        Swal.fire({
          type: 'success',
          title: 'Success',
          text: 'External User Added',
        });
        this.externalUserAddForm.reset();
        this.router.navigate(['/staff-list']);
      }
    })
  }

}
