import { ToastrService } from 'ngx-toastr';
import { Component, OnInit } from '@angular/core';
import { RolePermissionService } from '../role-permission.service';
import * as $ from 'jquery';
import { SettingService } from '@app/modules/settings/setting.service';
import { throwError } from 'rxjs';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-role-permission-add',
  templateUrl: './role-permission-add.component.html',
  styleUrls: ['./role-permission-add.component.scss'],
  providers: [RolePermissionService, SettingService]
})
export class RolePermissionAddComponent implements OnInit {

  roles = [];
  permissions = [];
  roleId;
  permissionNameId;
  permissionsForRole = [];

  constructor(
    private toastr: ToastrService,
    private rolePermissionService: RolePermissionService,
    private settingService: SettingService,
    private ngxService: NgxUiLoaderService) {

    this.settingService.getAllRoles().subscribe((response: any) => {
      this.roles = response;
    });

    this.rolePermissionService.getAllPermissions().subscribe((response: any) => {
      this.permissions = response;
    })
  }

  ngOnInit() {
  }

  getRollId(id) {
    this.roleId = id;
    this.clear();
    this.rolePermissionService.existingPermissionsofRole(id).subscribe((response: any) => {
      response.forEach(permission => {
        this.permissionsForRole.push({
          'roleId': this.roleId,
          'permissionId': permission.id,
          'isActive': true
        });
        $('#permissionName-' + permission.id).prop('checked', true);
        this.checkUnCheckPermissionGroup(permission.permissionGroupId)
      });
    })
  }

  clear() {
    $('input[type="checkbox"]').prop('checked', false);
    this.permissionsForRole = [];
  }

  getPermissionNameId(event, id, groupId) {
    this.removePreviousAddedPermission(this.roleId, id);
    if (event.target.checked) {
      this.permissionsForRole.push({
        'roleId': this.roleId,
        'permissionId': id,
        'isActive': true
      })
    } else {
      this.permissionsForRole.push({
        'roleId': this.roleId,
        'permissionId': id,
        'isActive': false
      })
    }

    this.checkUnCheckPermissionGroup(groupId);
  }

  removePreviousAddedPermission(roleId, permissionId) {
    var index = this.permissionsForRole.findIndex(object => object['roleId'] == roleId && object['permissionId'] == permissionId);
    if (index >= 0) {
      this.permissionsForRole.splice(index, 1);
      // this.toastr.success("Permission Removed");
    }
  }

  submitRolePermissions() {
    this.ngxService.start();
    this.rolePermissionService.submitPermissionsForRole(this.permissionsForRole).subscribe((response: any) => {
      if (response == true) {
        this.ngxService.stop();
        Swal.fire(
          'Success',
          'Role Permission Updated.',
          'success'
        )
      }
    })
  }

  onGroupPermissionChange(event, id) {
    var selectedPermission = this.permissions.filter(i => i.groupId == id)[0].permissions;
    if (event.target.checked) {
      selectedPermission.forEach(permission => {
        if (this.permissionsForRole.findIndex(obj => obj.permissionId == permission.id) < 0) {
          this.permissionsForRole.push({
            'roleId': this.roleId,
            'permissionId': permission.id,
            'isActive': true
          })
          $('#permissionName-' + permission.id).prop('checked', true);
        }
      });
    } else {
      selectedPermission.forEach(permission => {
        var index = this.permissionsForRole.findIndex(object => object['roleId'] == this.roleId && object['permissionId'] == permission.id);
        if (index >= 0) {
          this.permissionsForRole.splice(index, 1);
          this.permissionsForRole.push({
            'roleId': this.roleId,
            'permissionId': permission.id,
            'isActive': false
          })
        }
        $('#permissionName-' + permission.id).prop('checked', false);
      });
    }

  }

  checkUnCheckPermissionGroup(groupId) {
    var checkboxes = $('#group-' + groupId).siblings().find('input');
    var checkboxCount = checkboxes.length;
    var checkCount = 0;
    checkboxes.toArray().forEach(checkbox => {
      if ($(checkbox).prop('checked')) {
        checkCount++;
      }
    });
    $('#groupCheckbox-' + groupId).prop('checked', checkCount == checkboxCount);
  }

  checkReadOnly() {
    this.permissions.forEach(permission => {
      permission.permissions.forEach(p => {
        if (p.permissionName.toLowerCase().includes("read")) {
          this.permissionsForRole.push({
            'roleId': this.roleId,
            'permissionId': p.id,
            'isActive': false
          })
          $('#permissionName-' + p.id).prop('checked', true);
          this.checkUnCheckPermissionGroup(p.permissionGroupId)
        }
      });
    });

  }

  onReset() {
    $('#addRoleForm input[type="checkbox"]').prop('checked', false);
    this.permissionsForRole.forEach(permission => {
      permission.isActive = false;
    });
  }


}
